<template>
  <div class="tool">
    <el-tabs v-model="activeName" class="swap-tabs">
      <el-tab-pane label="归集" name="imputation" lazy>
        <div class="flex-row-center flex-1 flex-start">
          <div class="swap-info PB-20 imputation">
            <div class="swap-item">
              <div class="swap-item__top flex-row-between">
                <div class="flex-row-start align-center pointer">
                  <div class="flex-row-start align-center" @click="openTokensDialog('item1')">
                    <img v-lazy="getLogo(item1.address)" class="swap-item__top-icon MR-10"/>
                    <div class="swap-item__top-name text-overflow__1">{{item1.symbol}}</div>
                    <el-icon class="ML-5">
                      <ArrowDown />
                    </el-icon>
                  </div>
                  <el-icon class="ML-5" @click="copyHandler(item1.address)"><CopyDocument /></el-icon>
                </div>
                <div class="swap-item__desc text-20 flex-row-end align-center">
                  开启多对单<el-switch v-model="toOne" class="ML-10"/>
                </div>
              </div>
              
            </div>
            
            <textarea class="imputation-textarea" v-model="sendAddress" placeholder="请输入发送方私钥"></textarea>
            <img src="@/assets/images/transform.png" class="swap-transform pointer"/>
            <textarea class="imputation-textarea" v-model="getAddress" placeholder="请输入接收方地址" v-if="!toOne"></textarea>
            <input type="text" placeholder="请输入接收方钱包地址" v-model="getAddressOne" v-else class="imputation-input">
            <el-button class="swap-btn MT-10" :disabled="!sendAddress || (!getAddress && !getAddressOne)" :loading="imputationLoading" @click="imputationHandler">查询</el-button>
          </div>
          <div class="flex-1 ML-20" v-if="tableList.length > 0">
            <table class="imputation-table"> 
              <thead>
                <tr>
                  <th>发送地址</th>
                  <th>接收地址</th>
                  <th>{{item1.symbol}}余额</th>
                  <th>执行结果</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item, index) in tableList" :key="index">
                  <td>{{item.provider ? showAddress(item.provider.address) : '--'}}</td>
                  <td>{{item.getAddress ? showAddress(item.getAddress) : '--'}}</td>
                  <td>{{ item.balance || '--' }}</td>
                  <td>
                    <el-icon class="is-loading" v-if="item.loading">
                      <Loading />
                    </el-icon>
                    <template v-else>
                      {{ item.message }}
                    </template>
                  </td>
                </tr>
              </tbody>
            </table>
            <div class="flex-row-start align-center MB-20 MT-20">
              <div class="text-16 MR-30">发送数量</div>
              <div class="swap-item__input flex-1 flex-row-start align-center">
                <input class="text-right flex-1" v-model="price" placeholder="0.00">
              </div>
            </div>
            <div class="swap-slippage MT-10 MB-20">
              <div class="swap-slippage__select flex-row-between pointer">
                <div class="text-16">GWEI</div>
                <div class="flex-row-end">
                  <el-tag 
                    class="swap-slippage__select-tag" 
                    type="warning" 
                    :effect="gWei == item ? 'dark' : 'light'" 
                    @click="toggleGwei(item)"
                    v-for="item in gWeiList"
                    :key="item">
                    {{ item }}
                  </el-tag>
                  <input type="number" class="swap-slippage__select-input" placeholder="5" v-model.number="gWei">
                </div>
              </div>
            </div>
            <el-button class="swap-btn MT-10" :loading="loading" :disabled="!price" @click="imputationConfitm">确认</el-button>
          </div>
        </div>
        
      </el-tab-pane>
      <el-tab-pane label="批量Swap" name="swap" lazy>
        22222222222222
      </el-tab-pane>
    </el-tabs>
  </div>
  
  <Tokens ref="tokens" @change="tokenChange" @update="tokenUpdate"/>
</template>



<script>
  import Tokens from '../swap/modal/tokens.vue'
  import copy from 'copy-text-to-clipboard'
  import BLKTokenABI from '@/api/abis/IERC20.json'
  import { ElNotification } from 'element-plus'
  import { utils, BigNumber } from 'ethers'
  import { getBit, Tips, getLogo } from '../../libs/utils'
  import { mapGetters, mapActions } from 'vuex'
  import networkList from '@/api/abis/network.js'
  import i18n from '@/locals/index.js'
  import { toRaw } from 'vue'
  import axios from 'axios'
  const { t } = i18n.global
  export default {
    data() {
      return {
        t,
        utils,
        getLogo,
        getBit,
        toOne: true,
        price: '',
        activeName: 'imputation',
        progress: 10,
        loading: false,
        imputationLoading: false,
        gWei: 5,
        gWeiList: [5, 7, 10, 15, 20],
        type: '',
        // 发送方地址
        sendAddress: '',
        getAddressOne: '',
        // 接收方地址
        getAddress: '',
        tableList: [],
        // 第一个币种信息
        item1: {
          name: "BNB",
          symbol: "BNB",
          address: "0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c",
          balance: '',
          price: '',
          loading: false
        },
      }
    },
    components: {
      Tokens,
    },
    watch: {
      'currentNetwork.ChainID': {
        immediate: true,
        handler() {
          const { Token } = this.currentNetwork
          const item1 = Token[0]
          const item2 = Token[1] || Token[0]
          this.item1 = {...this.item1, ...item1}
          this.item2 = {...this.item2, ...item2}
          this.gWei = 5 * this.currentNetwork.GWEI
          this.gWeiList = [
            5 * this.currentNetwork.GWEI, 
            7 * this.currentNetwork.GWEI, 
            10 * this.currentNetwork.GWEI, 
            15 * this.currentNetwork.GWEI, 
            20 * this.currentNetwork.GWEI
          ]
        }
      },
    },
    directives: {
      bit: {
        updated(el) {
          el.value = el.value ? getBit(el.value, 5) : ''
        }
      }
    },
    computed: {
      ...mapGetters(['currentNetwork', 'networkList', 'currentDex']),
    },
    methods: {
      ...mapActions(['setCurrentDex', 'setConnectDialog']),
      showAddress(address) {
        return '0x...' + address.slice(-4)
      },
      async imputationHandler() {
        this.imputationLoading = true
        const sendList = this.sendAddress.split('\n')
        let getList = this.getAddress.split('\n')
        let checkStatus = true
        if (this.toOne) {
          if (this.getAddressOne.length != 42) {
            ElNotification({
              type: 'error',
              title: '温馨提醒',
              message: `接收方账号错误，请检查`
            })
            checkStatus = false
          }
        } else {
          for (let i = 0; i < getList.length; i++) {
            const item = getList[i];
            if (item.length != 42) {
              ElNotification({
                type: 'error',
                title: '温馨提醒',
                message: `第${i}行账号错误，请检查`
              })
              checkStatus = false
              break
            }
          }
        }
        if (!checkStatus) {
          return
        }
        const list = []
        for (let i = 0; i < sendList.length; i++) {
          const item = sendList[i];
          let provider
          let balance
          let status
          let message
          let contract
          let decimals
          try {
            provider = window.wallet.getPrivateKeyProvider(item)
            contract = window.wallet.initContract(this.item1.address, BLKTokenABI, provider)
            decimals = await contract.decimals()
            if (this.item1.address == this.currentNetwork.MainCoin.address) {
              balance = await provider.getBalance()
            } else {
              balance = await contract.balanceOf(provider.address)
            }
            balance = getBit(utils.formatUnits(balance.toString(), decimals), 6),
            status = 0
          } catch (error) {
            console.log(error)
            status = -1
            message = error.data ? error.data.message || error.message : error.reason || error.message
          }
          list.push({
            provider,
            getAddress: this.toOne ? this.getAddressOne : getList[i],
            balance,
            status,
            message,
            contract,
            decimals,
            loading: false
          })
        }
        this.tableList = list
        this.imputationLoading = false
      },
      async imputationConfitm() {
        this.tableList.forEach(async (item, index) => {
          item = toRaw(item)
          if (item.status == 0) {
            this.tableList[index].loading = true
            item.contract.transfer(
              item.getAddress, 
              utils.parseUnits(this.price.toString(), item.decimals),
              {
                gasPrice: utils.parseUnits(this.gWei.toString(), 9),
                gasLimit: 200000
              }
            ).then(tx => {
              tx.wait().then(() => {
                this.tableList[index].status = 1
                this.tableList[index].message = '发送成功'
                this.tableList[index].loading = false
              }).catch(err => {
                console.log(index, err)  
                this.tableList[index].status = 0
                this.tableList[index].message = '发送失败'
                this.tableList[index].loading = false
              })
            }).catch(err => {
              console.log(index, err)
              this.tableList[index].status = 0
              this.tableList[index].message = '发送失败'
              this.tableList[index].loading = false
            })
          }
        })        
      },
      // 更新币种信息
      tokenUpdate({name, symbol}) {
        this.item1.name = name
        this.item1.symbol = symbol
      },
      // 修改需要转换的币
      async tokenChange(data) {
        this.item1 = data
      },
      copyHandler(text) {
        copy(text)
        ElNotification({
          type: t('swap.success'),
          title: t('swap.success'),
          message: t('swap.copySuccess')
        })
      },
      toggleGwei(data) {
        this.gWei = data
      },
      openTokensDialog(type) {
        this.type = type
        this.$refs.tokens.open()
      },
      // 连接钱包
      loginFn() {
        this.setConnectDialog(true)
      }
    }
  }
  </script>

<style lang="scss" scoped>
  .imputation-table{
    width: 100%;
    border: 1px solid #2A2A2A;
    border-radius: 18px;
    border-spacing: 0;
    th{
      padding: 10px 0;
      border-right: 1px solid #2A2A2A;
      &:last-child{
        border-right: none;
      }
    }
    td {
      text-align: center;
      border-top: 1px solid #2A2A2A;
      border-right: 1px solid #2A2A2A;
      word-break: break-all;
      width: 25%;
      padding: 10px;
      &:last-child{
        border-right: none;
      }
    } 
  }
.imputation-textarea{
  margin: 10px 30px;
  background: none;
  border: 1px solid #898989;
  border-radius: 8px;
  width: calc(100% - 60px);
  height: 150px;
  resize: none;
  color: #fff;
  padding: 10px;
}
.imputation-input{
  margin: 10px 30px;
  background: none;
  border: 1px solid #898989;
  border-radius: 8px;
  width: calc(100% - 60px);
  height: 40px;
  color: #fff;
  padding: 10px;
}
.swap-item__input{
  width: 100%;
  height: 40px;
  border: 1px solid #2A2A2A;
  border-radius: 8px;
  background: none;
  font-size: 14px;
  padding: 0 20px;
  color: #fff;
  position: relative;
  >input{
    width: 100%;
    height: 100%;
    background: none;
    color: #fff;
    font-size: 16px;
  }
}
.swap-btn{
  margin: 0 auto;
  width: calc(100% - 60px);
  max-width: 350px;
  height: 60px;
  background: #FCBE2F;
  border: 1px solid #2A2A2A;
  border-radius: 18px;
  color: #fff;
  display: block;
  &.is-disabled{
    background: #e4e7ed;
  }
  &:hover, &:focus, &:active{
    background: #FCBE2F;
    color: #fff;
    border: none;
  }
}
</style>
<style lang="scss">
  .el-slider__bar{
    background: rgba(252, 190, 47, 1);
  }
  .el-slider__button{
    border-color: rgba(252, 190, 47, 1);
  }
  .swap-item__desc{
    font-weight: normal;
  }
  .swap-tabs{
    margin: 0 20px;
    width: auto;
    max-width: 100%;
    .el-tab-pane{
      display: flex;
      align-items: flex-start;
    }
    .el-tabs__item{
      color: #fff;
    }
    .el-tabs__item.is-active{
      color: #FCBE2F !important;
    }
    .el-tabs__active-bar{
      background: #FCBE2F !important;
    }
    .el-tabs__nav-wrap::after{
      display: none;
    }
    .el-tabs__active-bar{
      background: #fff;
    }
  }
  .swap-slippage__select-input{
    width: 40px;
    height: 20px;
    background: none;
    border-radius: 4px;
    border: 1px solid #898989;
    text-align: center;
    color: #fff;
  }
  .swap-slippage__select-tag{
    height: 20px;
    line-height: 18px;
    margin-right: 10px;
  }
  
  
  .swap-info{
    width: 410px;
    max-width: 100%;
    border: 1px solid #2A2A2A;
    border-radius: 18px;
    .swap-top{
      border-bottom: 1px solid rgba(42, 42, 42, 1);
      position: relative;
      text-align: center;
      padding: 10px;
      .swap-top__icon{
        position: absolute;
        left: 30px;
        top: 50%;
        transform: translateY(-50%);
        width: 24px;
        height: 19px;
        &.right{
          left: auto;
          right: 30px;
        }
        &.rpc{
          width: 28px;
          height: 28px;
          left: auto;
          right: 70px;
          top: 57%;
        }
        &.auto-rpc{
          width: 28px;
          height: 28px;
          left: auto;
          right: 20px;
          top: 57%;
        }
      }
      .swap-info__title{
        font-size: 20px;
        color: #fff;
      }
      .swap-info__title-info{
        font-size: 14px;
        color: rgba(255, 243, 204, 1);
      }
    }
    .swap-item{
      padding: 10px 20px;
    }
    .swap-item__top{
      font-size: 16px;
      font-weight: bold;
      .swap-item__top-icon{
        width: 30px;
        height: 30px;
      }
      .swap-item__top-wallet{
        width: 20px;
        height: auto;
      }
      .swap-item__top-name{
        max-width: 60px;
      }
    }
    .swap-transform{
      width: 40px;
      height: 40px;
      margin: 5px auto;
    }
    .text-yellow{
      font-size: 12px;
      color: rgba(240, 185, 11, 1);
    }
    .text-blue{
      font-size: 16px;
      color: rgba(31, 199, 213, 1);
    }
    .text-16{
      font-size: 16px;
    }
    .text-14{
      font-size: 14px;
    }
    .swap-slippage__select{
      background: #1B1B1B;
      border: 1px solid #2A2A2A;
      border-radius: 8px;
      padding: 10px 20px;
    }
    
  }
  
</style>